<template>
  <section class="code-area page">
    <div class="project-container">
      <div class="content-bar">Live preview : Projects.html</div>
      <div class="project-grid">
        <div class="project-element" v-for="project in projects" :key="project.name">
          <div class = 'project-icons'>
          <i class = 'folder-logo far fa-folder-open'></i>
          <a :href="project.url" target="_blank"><i class="github-logo fab fa-github"></i></a>
          </div>
          <h1 class="project-name">{{project.name}}</h1>
          <p class="project-description">{{project.description}}</p>
          <ul>
            <li v-for="language in project.languages" :key="language.name" class='language-element'>{{language}}</li>
          </ul>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import OctoKit from "@octokit/rest";
const octokit = new OctoKit();
import axios from 'axios'
export default {
  data() {
    return {
      projects: []
    };
  },
  created() {
    this.getProjects();
  },
  mounted(){
    console.log(this.projects);
  },
  methods: {
    getProjects: async function() {
      await octokit.repos.listForUser({ username: "latinrev" }).then(res => {
         res.data.map(curr => {
          axios.get(curr.languages_url)
          .then(languages => {
            this.projects = [...this.projects,{
              name: curr.name,
              description: curr.description,
              languages:Object.keys(languages.data),
              url: curr.html_url
            }]
          })
        })
        
      });
    }
  }
};
</script>

